import React from '../../../node_modules/react';
import Event from '../../Sections/Event';

const Mun = () => {
    return(
        <Event event="Model United Nation" img="mun.jpg" content="Le 11 juillet 2019, une date plus que mémorable qui a gravé l’histoire du CAP. En effet, en ce jour le club a vu l’apparition de la toute première édition de son nouvel évènement qui n’est autre que MUN, une première édition sous le nom de “ENPMUN” et portant comme devise ‘’ il ne s’agit plus de réfléchir mais plutôt d’agir’’.

MUN est l’abréviation de Model United Nations en anglais, c’est à dire Le modèle des Nations Unies. Il s’agit d’une simulation universitaire des Nations Unies dans laquelle les participants jouent le rôle de diplomates de différents pays. Ils essaient de résoudre des problèmes politiques contemporains en utilisant les perspectives et les ressources du pays qu’ils représentent.

Le MUN peut prendre la forme de tous les organes de l'ONU, tels que le Comité du désarmement, le Conseil des droits de l'homme, le Comité économique et social, etc. Les sujets peuvent aller de la prolifération non nucléaire aux droits des femmes en passant par le terrorisme ou le changement climatique, et incluent souvent des questions d’actualité importantes.

Pendant la simulation, le participant se retrouve complètement plongé dans l’univers de la diplomatie et va débattre des problèmes posés concernant le pays qu’il représente tout en assumant son rôle d'expert des affaires étrangères de manière professionnelle afin de trouver une résolution raisonnable et satisfaisante à la fin. Pour cela, il devra donc faire preuve de leadership et de capacité à négocier et à influencer les autres participants afin d’obtenir ultérieurement leur soutien en s’appuyant sur ses compétences dans le public speaking mais surtout sur l’art de convaincre pour tenter de gagner le trophée du meilleur délégué." />
    );
}

export default Mun;
